
h2{
    text-align: center;
    margin-left: 40px;
}
.graph-wrapper{

}

.graph{
    display: -ms-flexbox;
    display: flex;
    position: relative;
    max-width: 800px;
    height: 400px;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    margin: 40px auto;
}

.line{
    position: absolute;
    top:0;
    bottom: 0;
    width: 2px;
    background: rgba(0,0,0,0.1);
    left: 10%;
    z-index: -1;
}

.bar-text-content{
    height: 100%;
    width: 12%;
}

.text{
    height: 16.666667%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.bar-lines-container{
    width: 88%;
    height: 100%;
    background: red;
    position: relative;
    z-index: -25;
}

.bar{
    height: 16.666667%;
    background: #e05b42;
    z-index: 25;
    border-bottom: 1px solid #222;
    border-right: 1px solid #222;
}

.bar:last-child{
    border-bottom: none;
}

.markers{
    width: 100%;
    position: absolute;
    margin-top: 10px;
}

.markers > span{
    position: absolute;
    -webkit-transform: translateX(-7px);
        -ms-transform: translateX(-7px);
            transform: translateX(-7px);
    font-weight: 300;
    font-size: 14.5px;
}


